import './App.css';
import { BrowserRouter as Router, Routes, Route, Navigate } from 'react-router-dom';
import Announcement from './components/Announcement/Announcement';
import Navbar from './components/Navbar';
import Producto from './components/ItemContainer/ItemContainer';
import Home from './pages/index';
import Cursos from './pages/Cursos';
import Kiosco from './pages/Kiosco';
import Cafe from './pages/Cafe';
import Carrito from './pages/Carrito';
import Category from './pages/Categories';
import Login from './pages/Login';
import Register from './pages/Register';
import Footer from './components/Footer';
import Product from './pages/Product';

function App() {
  const user = false;

  return (
    <Router>
      <Announcement />
      <Navbar />
      <Routes>
          <Route exact path='/' element={<Home />} />
          <Route path='/:category/' element={<Category />} />
          <Route exact path='/cafe' element={<Cafe />} />
          <Route exact path='/cursos' element={<Cursos />} />
          <Route exact path='/kiosco' element={<Kiosco />} />
          <Route exact path='/kiosco/:id' element={<Producto />} />
          <Route exact path='/carrito' element={<Carrito />} />
          <Route exact path='/login' element={ user ? ( <Navigate to='/'/>) : ( <Login />) } />
          <Route exact path='/register' element={ user ? ( <Navigate to='/'/>) : ( <Register />) } />
          <Route exact path='/product' element={ <Product />} />
      </Routes>
      <Footer />
    </Router>
  );
}

export default App;

