import React, { useState } from 'react'
import BurguerButton from './BurguerButton'
import {
        Nav,
        NavbarContainer,
        NavLogo,
        MobileIcon,
        MenuItem,
        NavMenu,
        NavItem,
        NavLinks,
        NavBtn
    } from './NavbarElements'
import SearchIcon from '@mui/icons-material/Search';
import { Badge } from '@mui/material';
import ShoppingCartOutlinedIcon from '@mui/icons-material/ShoppingCartOutlined';
import PersonOutlineIcon from '@mui/icons-material/PersonOutline';
import { Link } from 'react-router-dom';

const Navbar = ({ click, handleClick }) => {
    const [ languageChange, setLanguageChange ] = useState(true);
    return (
        <Nav>
            <NavbarContainer>
                <NavLogo to='/'>DATA.</NavLogo>
                <MobileIcon onClick={handleClick}>
                    <BurguerButton />
                </MobileIcon>
                <NavMenu className={`links ${click ? 'active' : ''}`}>
                    <NavItem>
                        <NavLinks to='/cursos'><span>CURSOS</span></NavLinks>
                    </NavItem>
                    <NavItem>
                        <NavLinks to='/cafe'><span>CAF</span></NavLinks>
                    </NavItem>
                    <NavItem>
                        <NavLinks to='/kiosco'><span>KIOSCO</span></NavLinks>
                    </NavItem>
                </NavMenu>
                <NavBtn>
                    <MenuItem>
                        <SearchIcon />
                    </MenuItem>
                    <Link to="/login" style={{ color: '#FFF' }}>
                        <MenuItem className='login-widget'>
                            <PersonOutlineIcon />
                        </MenuItem>
                    </Link>
                    <Link to="/carrito" style={{ color: '#FFF' }}>
                        <MenuItem className='cart-widget'>
                            <Badge badgeContent={4} color="primary">
                                <ShoppingCartOutlinedIcon />
                            </Badge>
                        </MenuItem>
                    </Link>
                    <MenuItem onClick={() => setLanguageChange(!languageChange)}>
                        {languageChange === true ? "ES" : "EN"}
                    </MenuItem>
                </NavBtn>
            </NavbarContainer>
        </Nav>
    )
}

export default Navbar
