import React, { useState } from 'react'
import { useLocation } from 'react-router';
import styled from 'styled-components';
import ItemList from '../components/ItemProduct/ItemList';
// import ItemListContainer from '../components/ItemListContainer/ItemListContainer';

const Kiosco = () => {
  const location = useLocation();
  const cat = location.pathname.split("/")[2];
  const [filters,setFilters] = useState({});
  const [sort,setSort] = useState("Destacados");

  const handleFilters = (e) => {
    const value = e.target.value;
    setFilters({
      ...filters,
      [e.target.name]: value,
    });
  }

  return (
    <Container>
      <Title>KIOSCO</Title>
      <FilterContainer>
        <Filter><FilterText>Filtrar:</FilterText>
          <Select name="marcas" onChange={handleFilters}>
            <Option disabled defaultValue>
              Marcas
            </Option>
            <Option>Fellow</Option>
            <Option>Timemore</Option>
          </Select>
        </Filter>
        <Filter><FilterText>Ordenar</FilterText>
          <Select onChange={(e) => setSort(e.target.value)}>
            <Option value="Destacados">Destacados</Option>
            <Option value="Masvendidos">Ms vendidos</Option>
            <Option value="Preciomenor">Precio (asc)</Option>
            <Option value="Preciomayor">Precio (desc)</Option>
            <Option value="az">A-Z</Option>
            <Option value="za">Z-A</Option>
            <Option value="Fechaant">Fecha, antiguo a nuevo</Option>
            <Option value="Fechanue">Fecha, nuevo a antiguo</Option>
          </Select>
        </Filter>
      </FilterContainer>
      <ItemList cat={cat} filters={filters} sort={sort}/>
      {/* <ItemListContainer /> */}
    </Container>
  )
}

export default Kiosco

const Container = styled.div`
`
const Filter = styled.div`
  margin: 20px;
`;

const Title = styled.h1`
  text-align: center;
`

const FilterContainer = styled.div`
  display: flex;
  justify-content: space-between;
`;

const FilterText = styled.span`
  font-size: 20px;
  font-weight: 600;
  margin: 20px;
  display: inline-block;
`
const Select = styled.select`
  padding: 10px;
  margin-right: 26px;
`

const Option = styled.option``